%blueGrediantBackground {
  background: rgb(44, 206, 224);
  background: -moz-linear-gradient(
    -45deg,
    rgba(44, 206, 224, 1) 0%,
    rgba(125, 185, 232, 1) 100%
  );
  background: -webkit-linear-gradient(
    -45deg,
    rgba(44, 206, 224, 1) 0%,
    rgba(125, 185, 232, 1) 100%
  );
  background: linear-gradient(
    135deg,
    rgba(44, 206, 224, 1) 0%,
    rgba(125, 185, 232, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2ccee0', endColorstr='#7db9e8',GradientType=1 );
}
